<template>
  <n-space vertical>
    <n-card title="Gửi tin nhắn FB Version 1.2.1">
      <n-space vertical>
        <n-card>
          <n-space>
            <n-button type="primary" @click="loginWithFacebook"
              >Đăng nhập Facebook</n-button
            >
            <n-button v-if="loginStatus" type="primary" @click="getListPages"
              >Lấy danh sách trang</n-button
            >
          </n-space>
        </n-card>
        <div class=".equal-height-cards">
          <n-card>
            <n-space vertical>
              <n-form-item label="Tải lên" path="uploadValue">
                <n-upload
                  :default-file-list="fileList"
                  list-type="image-card"
                  :on-update:file-list="updateFileList"
                  @preview="handlePreview"
                >
                  <n-button>Ảnh</n-button>
                </n-upload>
              </n-form-item>
              <n-input
                v-model:value="message"
                autosize="{ minRows: 3, maxRows: 5 }"
                type="textarea"
                placeholder="Nội dung tin nhắn"
              />
            </n-space>
          </n-card>
        </div>
      </n-space>
    </n-card>
    <n-card title="Danh sách trang">
      <n-button style="float: right">{{ count }}</n-button>
      <n-data-table :columns="columns" :data="pages" />
    </n-card>
  </n-space>
</template>

<script>
import {
  NButton,
  NList,
  NListItem,
  NSpace,
  NDataTable,
  NInput,
  NCard,
  NImage,
  NText,
  useNotification,
  useMessage,
  NFormItem,
} from "naive-ui";
import { h, ref } from "vue";
import { ref as refb, uploadBytes, getDownloadURL, getStorage } from "firebase/storage";
import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyB3-qhi7iQKjNMiPW7BxDB7qq3nCZ6EyIg",
  authDomain: "sendmessage-77d09.firebaseapp.com",
  projectId: "sendmessage-77d09",
  storageBucket: "sendmessage-77d09.appspot.com",
  messagingSenderId: "244801679333",
  appId: "1:244801679333:web:17f9ab0930e8693dbdc90c",
  measurementId: "G-H6XEVYSJVT"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const storage = getStorage(app);

export default {
  name: "App",
  components: {
    NButton,
    NList,
    NListItem,
    NSpace,
    NDataTable,
    NInput,
    NCard,
    NImage,
    NText,
    NFormItem,
  },
  setup() {
    const message = useMessage();
    const notification = useNotification();
    const previewImageUrlRef = ref("");
    function handlePreview(file) {
      console.log(file);
      const { url } = file;
      previewImageUrlRef.value = url;
    }
    return {
      notify(text) {
        notification.create({
          title: "Gửi tin nhắn",
          content: text,
          type: "success",
        });
      },
      handlePreview,
      previewImageUrl: previewImageUrlRef,
      fileList: ref([]),
    };
  },
  data() {
    return {
      user: null,
      pages: [],
      message: "",
      imgPreview: "",
      img: "",
      imgUrl: "",
      listUpload: [],
      count: 0,
      loginStatus: false,
      conversations: [],
      loadingPages: {},
      columns: [
        {
          title: "Tên trang",
          key: "name",
        },
        {
          title: "Hành động",
          key: "actions",
          render: (row) => {
            return h(
              NButton,
              {
                strong: true,
                tertiary: true,
                size: "small",
                loading: this.loadingPages[row.id] || false,
                onClick: async () => {
                  this.loadingPages = { ...this.loadingPages, [row.id]: true };
                  await this.getPageConversations(row);
                  this.loadingPages = { ...this.loadingPages, [row.id]: false };
                },
              },
              { default: () => "Gửi" }
            );
          },
        },
      ],
    };
  },
  methods: {
    async updateFileList(fileList) {
      this.listUpload = fileList;
    },
    async handleFileUpload(index , file) {
      return new Promise(async (resolve) => {
        if (file) {
          console.log(file)
          const reader = new FileReader();
          reader.onload = (e) => {
            this.imgPreview = e.target.result;
          };
          reader.readAsDataURL(file);

          // Convert file to base64
          const storageRef = refb(storage, `images/upload_${index}.png`);
          // Upload the file to GitHub
          try {
            const snapshot = await uploadBytes(storageRef, file);
            const url = await getDownloadURL(snapshot.ref);
            resolve(url);
          } catch (error) {
            console.error('Error uploading file:', error);
            resolve(null);
          }
            
        }

      });
    },
    loadFacebookSDK() {
      return new Promise((resolve) => {
        if (document.getElementById("facebook-jssdk")) {
          resolve();
          return;
        }

        window.fbAsyncInit = function () {
          FB.init({
            appId: "862815729320540", // Replace with your Facebook App ID
            cookie: true,
            xfbml: true,
            version: "v12.0",
          });
          resolve();
        };

        (function (d, s, id) {
          var js,
            fjs = d.getElementsByTagName(s)[0];
          if (d.getElementById(id)) {
            return;
          }
          js = d.createElement(s);
          js.id = id;
          js.src = "https://connect.facebook.net/en_US/sdk.js";
          fjs.parentNode.insertBefore(js, fjs);
        })(document, "script", "facebook-jssdk");
      });
    },
    async loginWithFacebook() {
      await this.loadFacebookSDK();
      FB.login(
        (response) => {
          if (response.authResponse) {
            console.log("Welcome! Fetching your information.... ");
            FB.api("/me", { fields: "name,email" }, (response) => {
              this.loginStatus = true;
            });
          } else {
            this.loginStatus = false;
          }
        },
        {
          scope:
            "public_profile,email,pages_show_list,pages_read_engagement,pages_messaging,pages_messaging_subscriptions,pages_manage_metadata",
        }
      );
    },
    async getListPages() {
      FB.api("/me/accounts", "GET", {}, (response) => {
        this.pages = response.data;
      });
    },
    formatMessage(msg) {
      let message = msg;
      const stringIcon = message.match(/{(.*?)}/g) || [];
      if (stringIcon.length > 0) {
        let temp = stringIcon[0];
        let listIcon = temp.split("|");
        let iconId = 0;
        stringIcon.forEach(() => {
          let currentIcon = listIcon[iconId].replace("{", "").replace("}", "");
          message = message.replace(temp, currentIcon);
          if (iconId == listIcon.length - 1) {
            iconId = 0;
          } else {
            iconId++;
          }
        });

        return message;
      } else {
        return message;
      }
    },
    async getPageConversations(page) {
      this.count = 0;
      return new Promise(async (resolve) => {
        try {
          const limit = 100; // Specify the number of records per page
          let nextPage = `/${page.id}/conversations?limit=${limit}&`;
          const access_token = page.access_token; // Ensure access_token is correctly assigned
          const fields = "id,participants";
          while (nextPage) {
            await new Promise((resolve) => {
              FB.api(nextPage, "GET", { access_token, fields }, async (response) => {
                // Pass access_token in the API call
                if (response && !response.error) {
                  let users = response.data.map((conversation) => {
                    return conversation.participants.data.find(
                      (participant) => participant.id !== page.id
                    );
                  });
                  for (const user of users) {
                    if (user && user.id) {
                      this.count++;
                      await this.sendMessageToAllConversations(
                        page.id,
                        user.id,
                        access_token
                      );
                    }
                  }
                  // await this.sendMessageToAllConversations(page.id,response.data, access_token);
                  nextPage =
                    response.paging && response.paging.next ? response.paging.next : null;
                  resolve();
                } else {
                  console.error("Error fetching conversations:", response.error);
                  nextPage = null;
                  resolve();
                }
              });
            });
          }
          this.notify("Đã gửi tin nhắn cho tất cả người dùng");
          resolve();
        } catch (error) {
          console.error("Error fetching conversations:", error);
          resolve();
        }
      });
    },
    async convertFileToBase64(file) {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onload = (e) => {
          resolve(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    async SendImageToConservation(index, file, page_id, part_id, pageAccessToken) {
      let url = await this.handleFileUpload(index, file);
      if(!url) {
        return;
      }
      FB.api(
        `/${page_id}/messages`,
        "POST",
        {
          message: {
            attachment: {
              type: "image",
              payload: {
                is_reusable: true,
                url: url
              }
            }
          },
          messaging_type: "MESSAGE_TAG",
          tag: "ACCOUNT_UPDATE",
          recipient: {
            id: part_id,
          },
          access_token: pageAccessToken,
        },
        (response) => {
          if (response && !response.error) {
            console.log(`Message sent to conversation ${part_id}`);
          } else {
            console.error(
              `Error sending message to conversation ${part_id}:`,
              response.error
            );
          }
          resolve();
        }
      );
    },
    async sendMessageToAllConversations(page_id, part_id, access_token) {
      await new Promise((resolve) => {
        // Send text message
        FB.api(
          `/${page_id}/messages`,
          "POST",
          {
            message: {
              text: this.formatMessage(this.message),
            },
            messaging_type: "MESSAGE_TAG",
            tag: "ACCOUNT_UPDATE",
            recipient: {
              id: part_id,
            },
            access_token: access_token,
          },
          (response) => {
            if (response && !response.error) {
              console.log(`Text message sent to conversation ${part_id}`);
            } else {
              console.error(
                `Error sending text message to conversation ${part_id}:`,
                response.error
              );
            }
            resolve();
          }
        );
      });
      if (this.listUpload && this.listUpload.length > 0) {
        await new Promise(async (resolve) => {
            for (const [index, file] of this.listUpload.entries()) {
              await this.SendImageToConservation(
                index,
                file.file,
                page_id,
                part_id,
                access_token
              );
            }

          resolve()
        });
      }
    },
  },
};
</script>

<style>
.equal-height-cards {
  display: flex;
}

.equal-height-cards > .n-card {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.equal-height-cards > .n-card .n-space {
  flex: 1;
}
</style>
